<template>
	<div class="Document">
		<Modal :title="translate.TR_CHOOSE_PHOTO" v-if="modal_banner">
			<div class="mmodal">
				<div class="mbanner">
					<img :src="new_banner" v-if="new_banner">
				</div>

				<div class="mlink">
					<input type="text" :placeholder="translate.TR_PLACE_URL" v-model="url_new_banner" />
					<p @click="LoadBanner(url_new_banner)">{{translate.TR_LOAD}}</p>
					<p @click="ChangeBanner">{{translate.TR_CHANGE}}</p>
					<p @click="SetModalBanner(false)">{{translate.TR_CANCEL}}</p>
				</div>

				<div class="mlist">
					<p>{{translate.TR_OR_PREVIEW}}:</p>

					<div>
						<div class="mbanner" v-for="(image, index) in list_banners" :key="`banners_${index}`">
							<img :src="image" @click="LoadBanner(image)">
						</div>
					</div>
				</div>
			</div>
		</Modal>

		<Modal :title="translate.TR_SAVE_DOCUMENT" v-if="is_saving">
			<div class="mmodal" v-if="!loading_save">
				<div class="mbanner">
					<img :src="banner">
				</div>

				<div class="msave">
					<input type="text" :placeholder="translate.TR_NAME_DOCUMENT" v-model="name_document" />
					<div>
						<p @click="SaveDocument">{{translate.TR_CONFIRM}}</p>
						<p @click="SetIsSaving(false)">{{translate.TR_CANCEL}}</p>
					</div>
				</div>
			</div>

			<div class="mmodal" v-else>
				<Loading :text="translate.TR_SAVING" v-if="!save_error" />

				<div v-else>
					<p class="errorX">X</p>
					<p class="errorText">{{save_error_msg}}</p>

					<div class="msave">
						<div>
							<p @click="CloseSave">Sair</p>
						</div>
					</div>
				</div>
			</div>
		</Modal>

		<div class="control">
			<header>
				<h2 v-if="type == 'template'">{{translate.TR_TEMPLATE}} {{name_document}}</h2>
				<h2 v-if="type == 'document'">{{translate.TR_DOCUMENT}} {{name_document}}</h2>
			</header>

			<section v-if="readonly">
				<div class="option" @click="() => $router.back() ">
					<p>{{translate.TR_BACK}}</p>
					<i class="fas fa-angle-right"></i>
				</div>
			</section>

			<section v-else>
				<div class="option" @click="SetModalBanner(true)">
					<p>{{translate.TR_CHANGE_BANNER}}</p>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="AddSignature">
					<p>{{translate.TR_ADD_SIG}}</p>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="RemoveSignature">
					<p>{{translate.TR_DEL_SIG}}</p>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="SetIsSaving(true)">
					<p>{{translate.TR_SAVE}}</p>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="() => $router.back() ">
					<p>{{translate.TR_BACK}}</p>
					<i class="fas fa-angle-right"></i>
				</div>
			</section>
		</div>

		<div class="pages">
			<section class="page">
				<img class="triangulo" src="img/canto.png" alt="">

				<header>
					<div class="banner">
						<img :src="banner" alt="">
					</div>

					<input class="title" type="text" v-model="title" spellcheck="false" :readonly="readonly" />
					<input class="date" type="text" v-model="date" spellcheck="false" :readonly="readonly" />
				</header>

				<div class="text">
					<input class="subtitle" v-model="subtitle" type="text" :readonly="readonly" />

					<textarea ref="text" v-model="body" @input="AutoResize" :placeholder="translate.TR_TEXT_HERE" :readonly="readonly"></textarea>
				</div>

				<img src="img/rodape.png" alt="" class="rodape">
				<div class="createInfo">
					<p v-if="created_by"> {{translate.TR_CREATED_BY}} {{created_by}} </p>
					<p v-if="updated_by"> {{translate.TR_UPDATED_BY}} {{updated_by}} </p>
					<p v-if="finish_by"> {{translate.TR_FINISH_BY}} {{finish_by}} </p>
				</div>
			</section>

			<section class="page" v-if="!readonly || images.length > 0">
				<img class="triangulo" src="img/canto.png" alt="">

				<div class="text">
					<div class="images" v-for="(image, index) in images" :key="`image_${index}`">
						<div class="delete" v-if="!readonly">
							<i class="fas fa-eraser" @click="RemoveImage(index)"></i>
							<p>{{translate.TR_DELETE_IMG}}</p>
						</div>
						<img :src="image">
					</div>

					<div v-if="!readonly">
						<div class="insert" @click="SetIsInserting(true)" v-if="!is_inserting">
							<i class="far fa-image"></i>
							<p>{{translate.TR_CLICK_IMG}}</p>
						</div>

						<div class="new-image" v-else>
							<p>{{translate.TR_TEXT_INSERT_IMG}}</p>
							<input type="text" :placeholder="translate.TR_PUT_URL" v-model="new_image" />
							<p class="button" @click="InsertImage">{{translate.TR_INSERT}}</p>
						</div>
					</div>
				</div>

				<img src="img/rodape.png" alt="" class="rodape">
				<div class="createInfo">
					<p v-if="created_by"> {{translate.TR_CREATED_BY}} {{created_by}} </p>
					<p v-if="updated_by"> {{translate.TR_UPDATED_BY}} {{updated_by}} </p>
					<p v-if="finish_by"> {{translate.TR_FINISH_BY}} {{finish_by}} </p>
				</div>
			</section>

			<section class="page">
				<img class="triangulo" src="img/canto.png" alt="">

				<div class="text">
					<div class="assinatura" v-for="(signature, index) in signatures" :key="`signature_${index}`">
						<input class="name" type="text" :placeholder="translate.TR_EXAMPLE_NAME" v-model="signatures[index].name" :readonly="readonly">
						<input class="job" type="text" :placeholder="translate.TR_EXAMPLE_INFO" v-model="signatures[index].info" :readonly="readonly">
					</div>
				</div>

				<img src="img/rodape.png" alt="" class="rodape">
				<div class="createInfo">
					<p v-if="created_by"> {{translate.TR_CREATED_BY}} {{created_by}} </p>
					<p v-if="updated_by"> {{translate.TR_UPDATED_BY}} {{updated_by}} </p>
					<p v-if="finish_by"> {{translate.TR_FINISH_BY}} {{finish_by}} </p>
				</div>
			</section>
		</div>
	</div>
</template>

<script>
	import Modal from "@/components/Modal.vue";
	import Loading from "@/components/Loading.vue";
	import axios from "axios";

	export default {
		components: {
			Modal,
			Loading,
		},
		props: {
			type: {},
			action: {},
			id: {},
		},
		data() {
			return {
				is_inserting: false,

				name_document: "",
				title: "Document Title",
				date: "April 16, 2020",
				subtitle: "YOUR TITLE",
				body: "",
				images: [],
				signatures: [
					{
						name: "",
						info: "",
					},
				],
				created_by: "",
				updated_by: "",
				finish_by: "",

				new_image: "",

				modal_banner: false,
				banner: this.$store.state.server_banner,
				url_new_banner: "",
				new_banner: "",
				list_banners: [],

				is_saving: false,
				loading_save: false,
				save_error: false,
				save_error_msg: "",

				readonly: false,

                translate: {},
			};
		},

		methods: {
			AutoResize: function () {
				const el = this.$refs.text;

				if (el) {
					// el.style.height = 'fit-content';
					el.style.height = "2.2vh";
					el.style.height = el.scrollHeight + "px";

					// el.rows = "100";
				}
			},

			SetIsInserting: function (value) {
				this.is_inserting = value;
			},

			SetModalBanner: function (value) {
				this.modal_banner = value;
			},

			SetIsSaving: function (value) {
				this.is_saving = value;
			},

			SetLoadingSave: function (value) {
				this.loading_save = value;
			},

			CloseSave: function () {
				this.SetIsSaving(false);
				this.SetLoadingSave(false);
			},

			InsertImage: function () {
				this.images.push(this.new_image);
			},

			RemoveImage: function (index) {
				this.images.splice(index, 1);
			},

			AddSignature: function () {
				this.signatures.push({
					name: "",
					info: "",
				});
			},

			RemoveSignature: function () {
				if (this.signatures.length > 0) this.signatures.pop();
			},

			LoadBanner: function (url) {
				this.new_banner = url;
			},

			ChangeBanner: function () {
				if (this.new_banner) {
					this.banner = this.new_banner;

					if (!this.list_banners.includes(this.new_banner)) {
						this.list_banners = [this.new_banner, ...this.list_banners];
						localStorage.list_banners = JSON.stringify(this.list_banners);
					}

					this.SetModalBanner(false);
				}
			},

			SaveDocument: function () {
                if(!this.name_document)
                    return;

				this.SetLoadingSave(true);

				let document_id = null;
				let type = this.type == "template" ? 1 : 0;

				if (this.action == "edit") {
					document_id = this.id;
				}

				this.SendToClient("SaveDocument", {
					document: {
						name_document: this.name_document,
						title: this.title,
						date: this.date,
						subtitle: this.subtitle,
						body: this.body,
						images: this.images,
						signatures: this.signatures,
						banner: this.banner,
						type: type,
					},
					document_id: document_id,
				});
			},

			InitializeDocument: function () {
                this.translate = this.$store.state.translate;
                
				if (this.id != -1) {
					let doc = null;

					if (this.action == "create") {
						doc = this.$store.getters.GetTemplate(this.id);
					} else {
						doc =
							this.type == "template"
								? this.$store.getters.GetTemplate(this.id)
								: this.$store.getters.GetDocument(this.id);

						this.name_document = doc.infos.name_document;
						this.readonly = doc.infos.status == 0;
					}

					this.banner = doc.infos.banner;
					this.title = doc.infos.title;
					this.date = doc.infos.date;
					this.subtitle = doc.infos.subtitle;
					this.body = doc.body;
					this.images = doc.images;
					this.signatures = doc.signatures;

					this.created_by = doc.infos.created_by;
					this.updated_by = doc.infos.updated_by;
					this.finish_by = doc.infos.finish_by;
				} else {
					this.title = this.translate.TR_TITLE;
					this.date = this.translate.TR_DATE;
					this.subtitle = this.translate.TR_SUBTITLE;
				}
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (DocumentView): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;

				const lua = event.data;
				if (lua.confirm_action) {
					if (this.type == "template") this.$router.back();
					else this.$router.go(-2);
				} else if (lua.cancel_action) {
					this.save_error = true;
					this.save_error_msg = lua.cancel_msg;
				}
			},
		},

		mounted: function () {
			this.AutoResize();
		},

		created: function () {
			if (localStorage.list_banners) {
				this.list_banners = JSON.parse(localStorage.list_banners);
			}

			this.InitializeDocument();

			window.addEventListener("message", this.ReceiveLua);
		},

		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style>
	.Document {
		width: 120vh;
		min-height: 90vh;

		/* background: whitesmoke; */

		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;

		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);

		overflow: hidden;
	}

	.control {
		width: 30vh;
		height: 50vh;

		background: var(--theme-color);

		display: flex;
		flex-direction: column;

		border-radius: 5px;
		overflow: hidden;
	}

	.control header {
		width: 100%;
		padding: 1.2vh;

		/* border-bottom: 3px solid var(--theme-color); */
	}

	.control header h2 {
		font-size: 1.7vh;
		font-weight: 500;
		/* color: var(--theme-color); */
		color: white;
		white-space: nowrap;
	}

	.control section {
		flex: 1;
		background: rgb(255, 255, 255);

		padding-top: 5vh;
	}

	.control section .option {
		padding: 1vh 2vh;

		background: whitesmoke;
		margin-bottom: 2vh;

		font-size: 1.8vh;
		font-weight: 500;

		color: gray;

		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}

	.control section .option:hover {
		background: rgb(228, 228, 228);
		/* color: white; */
	}

	.pages {
		height: 90vh;
		overflow: scroll;
		overflow-x: hidden;
	}

	.pages::-webkit-scrollbar-track {
		background-color: #ffffff;
	}
	.pages::-webkit-scrollbar {
		width: 0.5vh;
		background: rgba(136, 20, 20, 0.8);
	}
	.pages::-webkit-scrollbar-thumb {
		background: rgba(139, 7, 7, 0.8);
		border-radius: 20px;
	}

	.page {
		width: 75vh;
		min-height: 90vh;

		background: white;

		position: relative;

		padding: 5vh;
		margin-bottom: 5vh;
	}

	.triangulo {
		position: absolute;

		top: 0%;
		right: 0%;

		width: 23vh;
	}

	.rodape {
		position: absolute;

		bottom: 2vh;
		right: 0%;

		width: 100%;
	}

	.pages header {
		/* background: black; */
		width: 30vh;

		display: flex;
		flex-direction: column;
	}

	.pages header .banner {
		width: 12vh;
		height: 12vh;
		border-radius: 50%;
		overflow: hidden;

		margin: auto;
	}

	.pages header .banner img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.pages header .title {
		font-size: 3vh;
		margin-top: 2vh;

		font-weight: bold;

		color: rgb(91, 91, 245);
	}

	.pages header .date {
		font-size: 2vh;
		font-weight: 600;
		color: #d66366;
	}

	.images {
		width: 100%;
		min-height: 30vh;
		height: 50vh;

		display: flex;
		justify-content: center;
		align-items: center;

		/* overflow: hidden; */

		position: relative;

		margin-bottom: 1vh;
	}

	.images img {
		max-width: 100%;
		max-height: 100%;

		/* object-fit: cover; */
	}

	.images:hover .delete {
		display: flex;
	}

	.delete {
		position: absolute;

		top: 0%;
		left: 0%;

		width: 100%;
		height: 100%;

		background: rgba(24, 24, 24, 0.836);
		color: white;
		display: none;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.delete i {
		font-size: 10vh;
	}

	.delete p {
		font-size: 2vh;
		margin-top: 1vh;
	}

	input,
	textarea {
		border: none;
		outline: none;
	}

	input:focus,
	textarea:focus {
		border: 1px solid rgb(130, 130, 230);
		border-radius: 5px;
	}

	.text {
		display: flex;
		flex-direction: column;

		padding: 2vh;
		padding-top: 8vh;
		padding-bottom: 10vh;
	}

	.subtitle {
		font-size: 2.5vh;
		font-weight: 500;
	}

	.text textarea {
		min-height: 40vh;
		margin-top: 2vh;

		font-size: 1.8vh;

		padding: 0 2vh;
	}

	.insert {
		border: 4px dashed gray;
		width: 80%;
		margin: auto;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		padding: 5vh;
		border-radius: 10px;

		font-size: 2vh;
		font-weight: 500;
		color: gray;
	}

	.insert:hover {
		background: whitesmoke;
	}

	.insert i {
		font-size: 10vh;
	}

	.new-image {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		font-size: 2vh;

		margin-top: 2vh;
	}

	.new-image input {
		background: whitesmoke;
		width: 100%;
		text-align: center;
		padding: 1vh;
		font-size: 2vh;

		margin-top: 1vh;
	}

	.new-image .button {
		background: var(--theme-color);
		color: white;
		padding: 0.5vh 2vh;

		border-radius: 3px;
		margin-top: 1vh;
	}

	.assinatura {
		display: flex;
		flex-direction: column;
		align-items: center;

		margin-bottom: 3vh;
	}

	.assinatura .name {
		width: 50vh;
		border-bottom: 2px solid black;

		text-align: center;
		padding-bottom: 1vh;

		font-family: "Dancing Script", cursive;
		font-size: 4.5vh;
		font-weight: bold;
	}

	.assinatura .job {
		width: 50vh;
		text-align: center;
		font-size: 2vh;
		margin-top: 0.5vh;
	}

	.mmodal {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.mmodal div {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.mbanner {
		width: 10vh;
		height: 10vh;
		min-width: 10vh;
		min-height: 10vh;

		background: rgb(211, 211, 211);

		border-radius: 50%;
		overflow: hidden;
	}

	.mbanner img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.mlink {
		width: 100%;
		margin-top: 2vh;
	}

	.mlink input {
		width: 100%;
		padding: 1vh;
		text-align: center;
		font-size: 1.5vh;
	}

	.mlink p {
		background: var(--theme-color);
		color: white;
		padding: 1vh 2vh;

		min-width: 15vh;
		text-align: center;
		border-radius: 3px;
		margin-top: 2vh;
	}

	.mlist {
		margin-top: 3vh;
	}

	.mlist p {
		font-size: 1.8vh;
		color: gray;
	}

	.mlist .mbanner {
		margin-top: 1.5vh;
	}

	.mlist > div {
		height: 20vh;
		width: 100%;
		overflow-y: scroll;

		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
	}

	.mlist > div::-webkit-scrollbar-track {
		background-color: #ffffff;
	}
	.mlist > div::-webkit-scrollbar {
		width: 0.5vh;
		background: rgba(136, 20, 20, 0.8);
	}
	.mlist > div::-webkit-scrollbar-thumb {
		background: rgba(139, 7, 7, 0.8);
		border-radius: 20px;
	}

	textarea::-webkit-scrollbar {
		width: 0vh;
		background: rgba(136, 20, 20, 0.8);
	}

	.msave {
		width: 100%;
		margin-top: 2vh;
	}

	.msave input {
		width: 100%;
		padding: 1vh;
		text-align: center;
		font-size: 1.5vh;
	}

	.msave > div {
		display: flex;
		flex-direction: row;
	}

	.msave p {
		background: var(--theme-color);
		color: white;
		padding: 1vh 2vh;

		min-width: 15vh;
		text-align: center;
		border-radius: 3px;
		margin: 2vh;
	}

	.errorX {
		font-size: 8vh;
		color: rgb(190, 39, 39);
		font-weight: bold;
	}
	.errorText {
		font-size: 1.8vh;
	}

	.createInfo {
		position: absolute;
        
		bottom: 0%;
		left: 0%;

		width: 100%;

		display: flex;
		flex-direction: row;
		justify-content: space-between;
		/* padding: 0 2vh; */

		font-size: 1.5vh;
		font-weight: 500;

		text-transform: lowercase;

        padding: 0 1vh;
	}
</style>