import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import DocumentView from '../views/DocumentView.vue'

import ListDocuments from '../components/ListDocuments.vue'
import ListTemplates from '../components/ListTemplates.vue'
import DocumentOptions from '../components/DocumentOptions.vue'

const routes = [
    {
        path: '/',
        redirect: '/home/documents',
    },
    {
        path: '/home',
        name: 'home',
        component: HomeView,
        children: [
            {
                path: "documents",
                name: "documents",
                component: ListDocuments,
            },
            {
                path: "templates",
                name: "templates",
                component: ListTemplates,
            },
            {
                path: "options/:id",
                name: "options",
                component: DocumentOptions,
                props: true,
            },
        ]
    },
    {
        path: '/document/:action/:type/:id',
        name: 'editor',
        component: DocumentView,
        props: true,
        // children: [
        //     {
        //         path: "documents",
        //         component: ListDocuments,
        //     },
        //     {
        //         path: "templates",
        //         component: ListTemplates,
        //     },
        // ]
    },
    //   {
    //     path: '/about',
    //     name: 'about',
    //     // route level code-splitting
    //     // this generates a separate chunk (about.[hash].js) for this route
    //     // which is lazy-loaded when the route is visited.
    //     component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    //   }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router
