<template>
	<section class="documents">
		<Modal :title="translate.TR_DELETE_TITLE2" v-if="is_deleting">
			<div class="mmodal" v-if="!loading_delete">
				<p class="mtext">{{translate.TR_DELETE_TEXT2}}</p>
				<div class="mbuttons">
					<p @click="DeleteDocument">{{translate.TR_YES}}</p>
					<p @click="SetIsDeleting(false)">{{translate.TR_NO}}</p>
				</div>
			</div>

			<div class="mmodal" v-else>
				<Loading :text="translate.TR_SAVING" v-if="!save_error" />

				<div v-else>
					<p class="errorX">X</p>
					<p class="errorText">{{save_error_msg}}</p>

					<div class="msave">
						<div>
							<p @click="CloseSave">{{translate.TR_EXIT}}</p>
						</div>
					</div>
				</div>
			</div>
		</Modal>

		<div class="buttons">
			<VueCustomTooltip :label="translate.TR_BACK_DOCUMENTS">
				<router-link :to="{name: 'documents'}">{{translate.TR_BACK}}</router-link>
			</VueCustomTooltip>

			<VueCustomTooltip :label="translate.TR_CREATE_TEMPLATE">
				<router-link :to="{name: 'editor', params: {id: -1, action: 'create', type: 'template'}}">{{translate.TR_NEW_TEMPLATE}}</router-link>
			</VueCustomTooltip>
		</div>

		<div class="options">
			<p>{{translate.TR_CHOOSE_TEMPLATE}}</p>
		</div>

		<div class="list-templates" v-if="!is_loading && templates.length > 0">
			<div class="item-temp" v-for="(template, index) in templates" :key="`template_${index}`">
				<router-link :to="{name: 'editor', params: {id: template.id, action: 'create', type: 'document'}}">
					<img :src="template.infos.banner" alt="">
				</router-link>
				<div class="temp-info">
					<VueCustomTooltip :label="translate.TR_CHOOSE_TEMPLATE2">
						<router-link :to="{name: 'editor', params: {id: template.id, action: 'create', type: 'document'}}">
							<p>{{template.infos.name_document}}</p>
						</router-link>
					</VueCustomTooltip>
				</div>

				<div class="temp-options">
					<VueCustomTooltip :label="translate.TR_EDIT_TEMPLATE">
						<router-link :to="{name: 'editor', params: {id: template.id, action: 'edit', type: 'template'}}">
							<i class="fas fa-edit"></i>
						</router-link>
					</VueCustomTooltip>

					<VueCustomTooltip :label="translate.TR_DELETE_TEMPLATE">
						<i class="fas fa-trash" @click="SetDeleteId(template.id)"></i>
					</VueCustomTooltip>

					<router-link :to="{name: 'editor', params: {id: template.id, action: 'create', type: 'document'}}">
						<i class="fas fa-angle-right"></i>
					</router-link>
				</div>
			</div>
		</div>

		<div class="loading" v-else-if="!is_loading">
			<p>{{translate.TR_EMPTY_TEMPS}}</p>
		</div>

		<div class="loading" v-else>
			<Loading :text="translate.TR_LOADING_TEMPS" size="9vh" textSize="1.7vh" />
		</div>
	</section>
</template>

<script>
	import axios from "axios";
	import Loading from "@/components/Loading.vue";
	import Modal from "@/components/Modal.vue";
	export default {
		components: {
			Modal,
			Loading,
		},

		data() {
			return {
				is_deleting: false,
				templates: [],

				is_loading: false,

				delete_id: -1,
				loading_delete: false,
				save_error: false,
				save_error_msg: "",

                translate: {},
			};
		},

		methods: {
			SetDeleteId: function (id) {
				this.SetIsDeleting(true);
				this.delete_id = id;
			},

			SetIsDeleting: function (value) {
				this.is_deleting = value;
			},

			SetLoadingDelete: function (value) {
				this.loading_delete = value;
			},

			CloseSave: function () {
				this.SetIsDeleting(false);
				this.SetLoadingDelete(false);
			},

			DeleteDocument: function () {
				this.SendToClient("DeleteDocument", {
					document_id: this.delete_id,
				});

				this.SetLoadingDelete(true);
			},

			LoadTemplates: function () {
				this.is_loading = true;
				this.SendToClient("LoadTemplates", {});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (ListTemplates): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;

				const lua = event.data;
				if (lua.load_templates) {
					this.$store.commit("SetTemplates", lua.templates);
					this.templates = lua.templates;
					this.is_loading = false;
				} else if (lua.confirm_action) {
					this.LoadTemplates();
                    this.CloseSave();
				} else if (lua.cancel_action) {
					this.save_error = true;
					this.save_error_msg = lua.cancel_msg;
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);

            this.translate = this.$store.state.translate;
            
            this.LoadTemplates();
		},

		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.documents {
		flex: 1;
		background: rgb(236, 236, 236);

		display: flex;
		flex-direction: column;

		padding: 2vh;
		padding-left: 10vh;
	}

	.buttons {
		display: flex;
		flex-direction: row;
		width: 100%;

		justify-content: flex-end;
	}

	.buttons a {
		padding: 1vh 2vh;
		margin-left: 1vh;

		background: var(--theme-color);
		color: white;
		border-radius: 3px;

		font-size: 1.7vh;
	}

	.options {
		margin-top: 3vh;
	}

	.options p {
		padding-bottom: 1vh;
		border-bottom: 3px solid var(--theme-color);
		color: var(--theme-color);

		width: max-content;
		text-align: center;

		font-size: 1.8vh;
	}
	.list-templates {
		display: flex;
		flex-direction: column;
		/* flex-wrap: wrap; */

		/* margin-top: 2vh; */
		padding-top: 2vh;

		width: 100%;
		max-height: 43vh;
		/* overflow: hidden; */
		overflow-y: scroll;
	}
	.list-templates::-webkit-scrollbar-track {
		background-color: #ffffff;
		width: 0vh;
	}
	.list-templates::-webkit-scrollbar {
		width: 0vh;
		background: rgba(136, 20, 20, 0.8);
	}
	.list-templates::-webkit-scrollbar-thumb {
		background: rgba(139, 7, 7, 0.8);
		border-radius: 20px;
	}
	.item-temp {
		width: 100%;
		height: 8vh;
		background: white;

		border-radius: 5px;
		border: 1px solid transparent;

		padding-left: 1vh;

		margin-bottom: 2vh;

		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;

		position: relative;

		/* overflow: hidden; */
	}

	.item-temp:hover {
		border: 1px solid var(--theme-color);
	}

	.item-temp img {
		width: 5vh;
		height: 5vh;
		border-radius: 50%;
	}

	.temp-info {
		margin-left: 1vh;
		flex: 1;

		font-size: 2vh;
		font-weight: 500;
	}

	.temp-info p {
		max-width: 30vh;
		overflow: hidden;
		white-space: nowrap;

		text-overflow: ellipsis;
	}

	.temp-options i {
		margin: 0 2.5vh;
		font-size: 1.8vh;
	}

	.mmodal {
		width: 100%;
		min-height: 15vh;

		display: flex;
		align-items: center;
		justify-content: center;

		/* background: black; */
	}

	.mtext {
		width: 80%;
		text-align: center;
		font-size: 1.7vh;
	}

	.mbuttons {
		width: 100%;
		display: flex;
		flex-direction: row;

		margin-top: 1vh;
	}

	.mbuttons p {
		margin: 1vh;
		padding: 1vh 2vh;

		background: var(--theme-color);
		color: white;
		border-radius: 3px;

		font-size: 1.8vh;
	}

	.mbuttons p:last-of-type {
		/* background: rgb(95, 95, 95); */
	}

	.loading {
		flex: 1;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
</style>