<template>
	<section class="documents">
		<div class="buttons">
			<VueCustomTooltip :label="translate.TR_CREATE_M_LABEL">
				<router-link to="/home/templates">{{translate.TR_NEW_DOCUMENT}}</router-link>
			</VueCustomTooltip>
		</div>

		<div class="options">
			<p>{{translate.TR_MY_DOCUMENTS}}</p>
		</div>

		<div v-if="!is_loading && documents.length > 0">
			<div class="search">
				<i class="fas fa-search"></i>
				<input type="text" :placeholder="translate.TR_SEARCH" v-model="search">
			</div>

			<div class="list-documents">
				<router-link class="item-doc" v-for="(document, index) in documents.filter(doc => doc.infos.name_document.toLowerCase().includes(search.toLowerCase()))" :key="`document_${index}`" :to="{name: 'options', params: {id: document.id}}">
					<p class="item-data">{{document.infos.name_document}}</p>
					<img :src="document.infos.banner" alt="">
					<div class="item-info">
						<p class="item-title">{{document.infos.title}}</p>
						<p class="item-status">{{document.infos.status == 1 ? translate.TR_OPENNED : translate.TR_CLOSED}}</p>
					</div>
				</router-link>
			</div>

		</div>

		<div class="loading" v-else-if="!is_loading">
			<p>{{translate.TR_EMPTY_DOCS}}</p>
		</div>

		<div class="loading" v-else>
			<Loading :text="translate.TR_LOADING_DOCS" size="9vh" textSize="1.7vh" />
		</div>
	</section>
</template>

<script>
	import axios from "axios";
	import Loading from "@/components/Loading.vue";

	export default {
		components: {
			Loading,
		},

		data() {
			return {
				search: "",

				documents: [],

				is_loading: false,
                translate: {},
			};
		},

		methods: {
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (ListDocuments): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;

				const lua = event.data;
				if (lua.load_documents) {
					this.$store.commit("SetDocuments", lua.documents);
					this.documents = lua.documents;
					this.is_loading = false;
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
            this.translate = this.$store.state.translate;
			this.is_loading = true;
			this.SendToClient("LoadDocuments", {});
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.documents {
		flex: 1;
		background: rgb(236, 236, 236);

		display: flex;
		flex-direction: column;

		padding: 2vh;
		padding-left: 10vh;
	}

	.buttons {
		display: flex;
		flex-direction: row;
		width: 100%;

		justify-content: flex-end;
	}

	.buttons a {
		padding: 1vh 2vh;
		margin-left: 1vh;

		background: var(--theme-color);
		color: white;
		border-radius: 3px;
	}

	.options {
		margin-top: 3vh;
	}

	.options p {
		padding-bottom: 1vh;
		border-bottom: 3px solid var(--theme-color);
		color: var(--theme-color);

		width: max-content;
		text-align: center;
	}

	.list-documents {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;

		margin-top: 2vh;

		max-height: 43vh;
		overflow-y: auto;
	}
	.list-documents::-webkit-scrollbar-track {
		background-color: #ffffff;
	}
	.list-documents::-webkit-scrollbar {
		width: 0.5vh;
		background: rgba(136, 20, 20, 0.8);
	}
	.list-documents::-webkit-scrollbar-thumb {
		background: rgba(139, 7, 7, 0.8);
		border-radius: 20px;
	}
	.item-doc {
		width: calc((100% / 3) - 2vh);
		height: 18vh;
		background: white;

		border-radius: 5px;
		border: 1px solid transparent;

		margin-right: 2vh;
		margin-bottom: 2vh;

		padding: 1vh 0;

		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		position: relative;

		overflow: hidden;
	}

	.item-doc:nth-of-type(3n) {
		margin-right: 0;
	}

	.item-doc:hover {
		border: 1px solid var(--theme-color);
	}

	.item-doc img {
		width: 6vh;
		height: 6vh;
		border-radius: 50%;
		margin-top: 1vh;
	}

	.item-data {
		font-size: 1.5vh;
		color: gray;
		width: 90%;
		text-align: center;
		padding-right: 1vh;

		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.item-info {
		width: 100%;
		text-align: center;

		margin-top: 1vh;
	}
	.item-title {
		font-size: 1.8vh;
		font-weight: 500;

		white-space: nowrap;
		text-overflow: ellipsis;
		width: 90%;
		margin: auto;
		overflow: hidden;
	}
	.item-status {
		font-size: 1.5vh;
		color: var(--theme-color);
		font-weight: 500;
		margin-top: 0.5vh;
	}

	.search {
		width: 100%;
		background: whitesmoke;
		border-radius: 50px;
		overflow: hidden;
		padding: 1vh;

		margin-top: 2vh;

		color: gray;

		display: flex;
		flex-direction: row;
		align-items: center;

		font-size: 1.5vh;
	}

	.search input {
		outline: none;
		width: 100%;

		font-size: 1.5vh;
		padding: 0 1vh;

		background: none;
	}

	.search input:focus {
		border: none !important;
	}

	.loading {
		flex: 1;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
</style>