<template>
	<div class="loading">
        <div class="loader" :style="{
            width: size,
            height: size,
            borderColor: color.secondary,
            borderTopColor: color.primary,
        }"></div>

        <p :style="{color:colorText, fontSize:textSize}">{{text}}</p>
	</div>
</template>

<script>
	export default {
        name: "LoadingVue",
        props: {
            color: {
                default: {
                    primary: "var(--theme-color)",
                    secondary: "#f3f3f3"
                }
            },

            size: {
                default: "12vh",
            },
            
            text: {
                default: "",
            },
            colorText: {
                default: "black",
            },
            textSize: {
                default: "1.7vh",
            }
        }
    };
</script>

<style scoped>
	.loader {
		border: 1vh solid #f3f3f3; /* Light grey */
		border-top: 1vh solid #3498db; /* Blue */
		border-radius: 50%;
		width: 12vh;
		height: 12vh;
		animation: spin 2s linear infinite;
	}

    p{
        margin-top: 1vh;
    }

	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
</style>