<template>
	<router-view v-if="openned" />
</template>

<script>
	import axios from "axios";

	export default {
		data() {
			return {
				openned: false,
			};
		},
		methods: {
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (Main): ${to} : ${msg}`)
					);
			},
			ReceiveLua: function (event) {
				if (!event || !event.data) return;

				const lua = event.data;
				if (lua.config) {
					this.$store.state.translate = lua.translate;
					this.$store.state.name_resource = lua.name_resource;
					this.$store.state.server_banner = lua.server_banner;

					this.SendToClient("ConfirmConfig", {});
				} else if (lua.open) {
					this.openned = true;
				} else if (lua.see_document) {
					this.$store.commit("SetDocuments", [lua.document]);
					this.$router.push({
						name: "editor",
						params: {
							id: lua.document.id,
							type: "document",
							action: "edit",
						},
					});
				}
			},

			Close: function () {
				this.SendToClient("Close", {});
				this.openned = false;
			},

			KeyPress: function (event) {
				const key = event.key;

				if (key == "Escape") {
					this.Close();
				}
			},
		},
		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			window.addEventListener("keydown", this.KeyPress);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
			window.removeEventListener("keydown", this.KeyPress);
		},
	};
</script>

