<template>
	<section class="documents">
		<Modal :title="translate.TR_DELETE_TITLE" v-if="is_deleting">
			<div class="mmodal" v-if="!loading_delete">
				<p class="mtext">{{translate.TR_DELETE_TEXT}}</p>
				<div class="mbuttons">
					<p @click="DeleteDocument">{{translate.TR_YES}}</p>
					<p @click="SetIsDeleting(false)">{{translate.TR_NO}}</p>
				</div>
			</div>

			<div class="mmodal" v-else>
				<Loading :text="translate.TR_SAVING" v-if="!save_error" />

				<div v-else>
					<p class="errorX">X</p>
					<p class="errorText">{{save_error_msg}}</p>

					<div class="msave">
						<div>
							<p @click="CloseSave">{{translate.TR_EXIT}}</p>
						</div>
					</div>
				</div>
			</div>
		</Modal>

		<Modal :title="translate.TR_FINISH_TITLE" v-if="is_finishing">
			<div class="mmodal" v-if="!loading_finish">
				<p class="mtext">{{translate.TR_FINISH_TEXT}}</p>
				<div class="mbuttons">
					<p @click="FinishDocument">{{translate.TR_YES}}</p>
					<p @click="SetIsFinishing(false)">{{translate.TR_NO}}</p>
				</div>
			</div>

			<div class="mmodal" v-else>
				<Loading :text="translate.TR_SAVING" v-if="!save_error" />

				<div v-else>
					<p class="errorX">X</p>
					<p class="errorText">{{save_error_msg}}</p>

					<div class="msave">
						<div>
							<p @click="CloseSave">{{translate.TR_EXIT}}</p>
						</div>
					</div>
				</div>
			</div>
		</Modal>

		<div class="buttons">
			<VueCustomTooltip :label="translate.TR_SEE_LIST">
				<router-link to="/home/documents">{{translate.TR_BACK}}</router-link>
			</VueCustomTooltip>
		</div>

		<!-- <div class="options">
			<p>Troca Robert and Jason</p>
		</div> -->

		<div class="box">
			<div class="side header">
				<div class="banner">
					<img :src="document.infos?.banner" alt="">
				</div>

				<div class="infos">
					<div>
						<p>{{document.infos?.name_document}}</p>
						<p>{{document.infos?.title}}</p>
					</div>
					<div>
						<p>{{document.infos?.date}}</p>
						<p>{{document.infos?.status == 1 ? translate.TR_OPENNED : translate.TR_CLOSED}}</p>
					</div>
				</div>
			</div>

			<div class="side right">
				<router-link class="option" :to="{name: 'editor', params: {id: document.id, type: 'document', action: 'edit'}}">
					<i class="fas fa-angle-left"></i>
					<p>{{translate.TR_OPEN}}</p>
					<i class="fas fa-angle-right"></i>
				</router-link>

				<div class="option" @click="SetIsFinishing(true)" v-if="document.infos.status == 1">
					<i class="fas fa-angle-left"></i>
					<VueCustomTooltip :label="translate.TR_FINISH_LABEL">
						<p>{{translate.TR_FINISH}}</p>
					</VueCustomTooltip>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="CreateCopy" v-else>
					<i class="fas fa-angle-left"></i>
					<p>{{translate.TR_CREATE_COPY}}</p>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="ShowDocument">
					<i class="fas fa-angle-left"></i>
					<VueCustomTooltip :label="translate.TR_SHOW_LABEL">
						<p>{{translate.TR_SHOW_DOCUMENT}}</p>
					</VueCustomTooltip>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="GiveDocument">
					<i class="fas fa-angle-left"></i>
					<VueCustomTooltip :label="translate.TR_GIVE_LABEL">
						<p>{{translate.TR_GIVE_DOCUMENT}}</p>
					</VueCustomTooltip>
					<i class="fas fa-angle-right"></i>
				</div>

				<div class="option" @click="SetIsDeleting(true)">
					<i class="fas fa-angle-left"></i>
					<p>{{translate.TR_DELETE}}</p>
					<i class="fas fa-angle-right"></i>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
	import Modal from "@/components/Modal.vue";
	import Loading from "@/components/Loading.vue";
	import axios from "axios";

	export default {
		components: {
			Modal,
			Loading,
		},

		props: {
			id: {
				default: -1,
			},
		},

		data() {
			return {
				is_deleting: false,
				loading_delete: false,
				save_error: false,
				save_error_msg: "",

				is_finishing: false,
				loading_finish: false,

				document: {},

				wait: false,

                translate: {},
			};
		},

		methods: {
			SetIsDeleting: function (value) {
				this.is_deleting = value;
			},

			SetLoadingDelete: function (value) {
				this.loading_delete = value;
			},

			CloseSave: function () {
				this.SetIsDeleting(false);
				this.SetLoadingDelete(false);

				this.SetIsFinishing(false);
				this.SetLoadingFinish(false);
			},

			DeleteDocument: function () {
				this.SendToClient("DeleteDocument", {
					document_id: this.id,
				});

				this.SetLoadingDelete(true);
			},

			FinishDocument: function () {
				this.SendToClient("FinishDocument", {
					document_id: this.id,
				});

				this.SetLoadingFinish(true);
			},

			SetIsFinishing: function (value) {
				this.is_finishing = value;
			},
			SetLoadingFinish: function (value) {
				this.loading_finish = value;
			},

			GiveDocument: function () {
				this.SendToClient("GiveDocument", {
					document_id: this.id,
				});
			},

			ShowDocument: function () {
				this.SendToClient("ShowDocument", {
					document_id: this.id,
				});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (DocumentOptions): ${to} : ${msg}`)
					);
			},

			CreateCopy: function () {
				if (this.wait) return;

				this.SendToClient("CreateCopy", {
					document_id: this.id,
				});

				this.wait = true;
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;

				const lua = event.data;
				if (lua.confirm_action) {
					this.wait = false;
					this.$router.back();
				} else if (lua.cancel_action) {
					this.save_error = true;
					this.save_error_msg = lua.cancel_msg;
					this.wait = false;
				}
			},
		},
		created: function () {
            this.translate = this.$store.state.translate;
			this.document = this.$store.getters.GetDocument(this.id);

			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.documents {
		flex: 1;
		background: rgb(236, 236, 236);

		display: flex;
		flex-direction: column;

		padding: 2vh;
		padding-left: 10vh;
	}

	.buttons {
		display: flex;
		flex-direction: row;
		width: 100%;

		justify-content: flex-end;
	}

	.buttons a {
		padding: 1vh 2vh;
		margin-left: 1vh;

		background: var(--theme-color);
		color: white;
		border-radius: 3px;
	}

	.box {
		width: 100%;
		flex: 1;

		/* background: white; */

		margin-top: 5vh;

		border-radius: 5px;
		/* border: 1px solid rgb(231, 231, 231); */

		display: flex;
		flex-direction: column;
	}

	.side {
		/* flex: 1; */

		width: 100%;

		background: white;
	}

	.right {
		flex: 1;

		background: none;

		padding: 2vh;
	}

	.header {
		display: flex;
		flex-direction: row;

		border-radius: 50px;
	}

	.banner {
		width: 10vh;
		height: 10vh;
		border-radius: 50%;

		overflow: hidden;
	}

	.banner img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.infos {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		font-size: 1.7vh;
		font-weight: 500;

		flex: 1;

		padding: 0 5vh;
		text-align: center;
	}

	.option {
		width: 100%;
		padding: 1vh;

		background: white;

		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		margin-top: 2vh;

		font-size: 1.8vh;
		font-weight: 500;
		color: gray;

		cursor: pointer;
	}

	.option:hover {
		color: black;
		background: whitesmoke;
		/* transition: all 1s; */
	}

	.mmodal {
		width: 100%;
		min-height: 15vh;

		display: flex;
		align-items: center;
		justify-content: center;

		/* background: black; */
	}

	.mtext {
		width: 80%;
		text-align: center;
		font-size: 1.7vh;
	}

	.mbuttons {
		width: 100%;
		display: flex;
		flex-direction: row;

		margin-top: 1vh;
	}

	.mbuttons p {
		margin: 1vh;
		padding: 1vh 2vh;

		background: var(--theme-color);
		color: white;
		border-radius: 3px;

		font-size: 1.8vh;
	}

	.mbuttons p:last-of-type {
		/* background: rgb(95, 95, 95); */
	}
</style>