<template>
    <div class="modal">
        <div class="header">
            <p>{{title}}</p>
        </div>

        <div class="slot">
            <slot></slot>
        </div>
    </div>
</template>

<script>
	export default {
        name: "ModalVue",
        props: {
            title: {
                type: String,
                default: "",
            }
        }
    };
</script>

<style scoped>
    .modal{
        position: absolute;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        z-index: 1;

        background: whitesmoke;

        /* padding: 5vh; */

        min-width: 50vh;
        min-height: 20vh;

        border: 1px solid rgb(231, 231, 231);

        animation: up 1s;
    }

    .header{
        width: 100%;
        background: white;
        border-bottom: 1px dashed rgb(218, 218, 218);

        padding: 1vh;
        font-size: 1.8vh;

        font-weight: 500;

        /* background: var(--theme-color); */
        /* color: white; */
    }

    .slot{
        padding: 1vh;
    }

    @keyframes up{
        0%{
            transform: translate(-50%, 100%);
        }
        100%{
            transform: translate(-50%, -50%);
        }
    }
</style>